.main-header {
    background-color: $clr1;
}

header {
    align-items: center;
    min-height: 100px;
    position: relative;
    .logo {
        font-size: em(40px);
        font-family: poppinsBlack;
        letter-spacing: 0.5px;
        background: linear-gradient(45deg, $clr2, $clr3, darken($clr3, 22%), $clr2);
        background-clip: text;
        text-shadow: 0 10px 10px $dark-03;
        background-position: 0%;
        background-size: 400%;
        color: transparent;

        animation: logo 18s linear infinite;
    }

    nav {
        z-index: 250;
        .main-nav {
            border-left: 2px solid #3335;
            position: absolute;
            background-color: $clr1;
            top: 100px;
            right: -20px;
            width: 0;
            height: calc(100vh - 160px);
            transition: 0.2s;
            overflow: hidden;
            body.menu-open & {
                width: 300px;
            }
        }

        button {
            height: 100px;
            width: 100px;
            display: grid;
            place-content: center;
            gap: 6px;
            background-color: transparent;
            float: right;
            position: relative;
            .line {
                transform-origin: center;
                background-color: $light;
                width: 36px;
                height: 3px;
                border-radius: 2em;
                position: absolute;
                top: calc(var(--i) * 8px + 50%);
                left: 50%;
                transform: translate(-50%, -50%);
                transition: 0.2s;
                body.menu-open & {
                    &:not(:nth-child(even)) {
                        transform: translate(-50%, -50%) rotate(-45deg);
                        top: 50%;
                    }

                    &:not(:nth-child(odd)) {
                        transform: translate(-50%, -50%) rotate(45deg);
                    }
                }
            }
        }

        a {
            color: $clr3;
            height: 100px;
            width: 100%;
            display: grid;
            place-content: center;
            transition: 0.3s;
            cursor: pointer;
            &.active {
                background-color: #eee5;
                font-size: 1.3em;
                color: $clr3;
                box-shadow: inset 0 -6px $clr3;
            }
        }
    }

    @media (min-width: 800px) {
        nav {
            button {
                display: none;
            }
            .main-nav {
                width: 300px;

                top: 0;
                background-color: transparent;
                height: auto;
                position: relative;
                display: flex;
                border-left: none;
            }
        }
    }
}

@keyframes logo {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 400%;
    }
}
