.transfer-funds {
    margin-block-start: 80px;
    text-align: center;
    // background-color: red;
    padding-block: 20px;
    padding: 30px;
    .toggle-transfer {
        position: relative;
        transition: 0.2s;

        &:hover {
            color: #5565d4;
            &::before {
                width: 100%;
            }
        }

        &::before {
            content: "";
            transition: 0.2s;
            width: 0%;
            background: #5565d4;
            height: 2px;
            bottom: -2px;
            left: 0;
            position: absolute;
        }
    }
    h2 {
        margin-block-end: 20px;
    }
    h3 {
        margin-block-start: 10px;
    }
    &:has(.recharge) {
        background-color: #eee;
        border-radius: 15px;
        box-shadow: 0 0 0 200vh #3335;
      
        .toggle-transfer {
            &:hover {
                color: #7bd16e;
            }
            &::before {
                background: #7bd16e;
            }
        }
    }
    form {
        display: flow-root;
        & * {
            margin-inline: 5px;
        }

        input {
            background-color: transparent;
            border: none;
            outline: none;
            box-shadow: 0 1px 0 #333;
            padding-inline-start: 5px;
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }
