.move-list {
    margin-block-start: 40px;
    max-height: 450px;
    overflow-y: auto;
}

.move-preview {
    &.recharge {
        background: linear-gradient(to right, #29f0b433, #5d9ff581);

        .amount::after {
            content: "Recharged";
            position: absolute;
            left: 50%;
            top: 70%;
            transform: translate(-50%, -50%) rotate(8deg) scale(0.5);
        }
    }
    &.transfer {
        background-color: #3331;
        background: linear-gradient(to left, #40e60e33, #e7636381);
    }
    box-shadow: 0 0 3px 1px #3335;
    text-transform: capitalize;
    margin-block-start: 10px;
    height: 140px;
    text-align: center;
    border-radius: 14px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 20px;
    place-content: center;
    position: relative;
    h2 {
        font-size: rem(20px);
        line-height: 140px;
        &:not(.amount) {
            z-index: 3;
        }
        &.amount {
            font-size: rem(40px);
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%) rotate(-8deg) scale(2);
            color: #eee9;

            &::after {
                content: "To:";
                position: absolute;
                left: 50%;
                top: 70%;
                transform: translate(-50%, -50%) rotate(8deg) scale(0.5);
            }
        }
    }
}
